import { Camera, CameraOptions } from '@ionic-native/camera';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Slides, ViewController, normalizeURL } from 'ionic-angular';

/**
 * Generated class for the InspectionCameraModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-inspection-camera-modal',
  templateUrl: 'inspection-camera-modal.html',
})
export class InspectionCameraModalPage {
  public insp;
  public fotos: any[] = [];
  preview = false;
  @ViewChild(Slides) slides: Slides;
  constructor(public navCtrl: NavController, public navParams: NavParams, private camera: Camera,
     public alertCtrl: AlertController, public viewCtrl: ViewController) {
    this.insp = this.navParams.get('insp');
    
    this.insp.img = this.insp.img.map((i) => i.replace(/(?:\r\n|\r|\n)/g,''));
    this.preview = navParams.get('preview');
    if (this.insp.img == undefined || this.insp.img == null || this.insp.img.length == 0){
      this.tirarFoto();
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad InspectionCameraModalPage');
  }

  tirarFoto() {
    const options: CameraOptions = {
      quality: 50,
      destinationType: this.camera.DestinationType.DATA_URL,
      saveToPhotoAlbum: true,
      mediaType: this.camera.MediaType.PICTURE,
      encodingType: this.camera.EncodingType.JPEG,
      targetHeight: 960,
      targetWidth: 720
    }

    this.camera.getPicture(options).then(
      imageData => {
        if(this.insp.img == undefined || this.insp.img == null){
          this.insp.img = [];
        }
        this.insp.img.push(imageData.replace(/(?:\r\n|\r|\n)/g,''));
      },
      err => {
        //this.displayErrorAlert(err);
      }
    );
  }

  displayErrorAlert(err){
    let alert = this.alertCtrl.create({
       title: 'Error',
       subTitle: 'Error while trying to capture picture',
       buttons: ['OK']
     });
     alert.present();
  }

  excluirFoto() {
    if (this.insp.img.length == 0){
      this.viewCtrl.dismiss(this.insp);
    }else {
      this.insp.img.splice(this.slides.getActiveIndex(), 1);
      this.slides.slidePrev();
    }
  }

  confirmarFoto() {
    this.viewCtrl.dismiss(this.insp);
  }

  sair() {
    this.viewCtrl.dismiss();
  }
}



